.programmingContainer {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 40px;
}
.programmingLeftColumn{
    width: 30%;
    font-weight: bold;
}
.programmingRightColumn{
    width: 52.5%; 
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    font-size: 18px;
}
.title {
    text-align: left;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: 100px;
}
.columnFontStyle {
    font-size: 20px;
    color: black;
}
.bar {
    width: 70%;
    background: #e0e0e0;
    height: 10px;
    border-radius: 2px;
    margin-top: 5px;
    margin-bottom: 20px;
}
.javaBar {
    width: 80%;
    background: #181818;
    height: 100%;
    border-radius: 2px;
}
.sqlBar {
    width: 80%;
    background: #181818;
    height: 100%;
    border-radius: 2px;
}
.jspBar {
    width: 70%;
    background: #181818;
    height: 100%;
    border-radius: 2px;
}
.reactBar {
    width: 60%;
    background: #181818;
    height: 100%;
    border-radius: 2px;
}
.pythonBar {
    width: 30%;
    background: #181818;
    height: 100%;
    border-radius: 2px;
}